<template>
  <div class="chatIcon">   
    <userspic :slidesPerView="slidesPerView" :users="users" :chat="chat" :invited="invited" :unseen="this.chat.getUnreadNotificationCount()"/>
  </div>
</template>

<style scoped lang="sass">
  .chatIcon
    width: 100%

</style>

<script>


import _ from "underscore";
import f from "@/application/functions";

export default {
  name: 'chatIcon',

  props : {
    chat : Object,
    slidesPerView : Number
  },

  computed:{
    users : function(){


        var users = _.filter(_.map(this.chat?.currentState.members || {}, (m) => {
            return this.$f.deep(this, '$store.state.users.' + this.$f.getmatrixid(m.name)) || null
        }), (m) => {
            return m && (_.toArray(this.chat.currentState.members).length > 2 || m.id !== this.core.user.userinfo?.id)
        })

        return users
      
    },

    invited : function(){

      var i = {}

      _.each(this.chat?.currentState.members, (m) => {
        if(m.membership === 'invite') i[this.$f.getmatrixid(m.name)] = true
      })

      return i
    },
    count: function (){
      // let currentUser = this.core.mtrx.client.credentials.userId
      // let lastReaded = this.chat._receipts['m.read'][currentUser].eventId
      // let events = this.chat.timeline
      // let indexEvent = events.findIndex(event => event.event.event_id === lastReaded)
      // console.log(lastReaded, "lastReaded event")
      // console.log(events, "lastReaded event")
      // console.log(indexEvent, "lastReaded index event")
      // console.log(events.splice(indexEvent), "array not REaded")
      return ''
    }
  }
}
</script>
